* {
    margin: 0;
    padding: 0;
    box-sizing: border-box; }

.feedback {
    width: auto;
    padding: 1rem;
    height: auto;
    border-radius: 0.3rem;
    top: 2rem;
    background-color: #c8c8c8;
    position: absolute;
    z-index: 9;
    display: flex;
    align-items: center;
    justify-content: center;
    visibility: visible;
    animation: enter-feedback 1s;
    animation-timing-function: ease-out;
    right: 2rem;

    &:after {
        content: '';
        position: absolute;
        width: 100%;
        height: 100%;
        border-radius: 0.3rem;
        background: linear-gradient(rgba(255, 255, 255, 0.5), rgba(22, 22, 22, 0.4)); }

    &__icon {
        position: absolute;
        top: 0.2rem;
        left: 0.2rem;
        cursor: pointer; }

    &__error {
        z-index: 99;

        &__message {
            color: red; } }

    &__warning {
        z-index: 99;

        &__message {
            color: orange; } } }

@keyframes enter-feedback {
    0% {
        // right: -14rem
        opacity: 0; }
    100% {
        opacity: 1;
        // right: 2rem
 } }        // width: 14rem
