* {
    margin: 0;
    padding: 0;
    box-sizing: border-box; }

.menu {
    position: absolute;
    width: auto;
    height: auto;
    border-radius: 0.3rem;
    background-color: #343434;
    color: #e6e6e6;
    align-self: center;
    text-align: center;
    border-color: black;
    animation: enter 1s forwards ease, fade 2s forwards 1.5s;

    &__item {
        cursor: pointer;
        width: 100%;
        height: 100%;
        text-align: center;
        border-radius: 0.3rem;

        &:hover {
            background-color: #212121;
            color: #cccccc; }
        &:after {
            content: '';
            position: absolute;
            background: linear-gradient(to bottom right,  rgba(255, 255, 255, 0),rgba(255, 255, 255, 0.5));
            bottom: 0%;
            border-radius: 0.3rem;
            left: 0%;
            opacity: 0.6;
            height: 100%;
            width: 100%;
            z-index: 2; } } }

@keyframes enter {
    0% {
        width: 0rem;
        font-size: 0rem; }
    100% {
        width: 5rem;
        font-size: 1rem; } }

@keyframes fade {
    0% {
        opacity: 1; }
    100% {
        opacity: 0; } }

@keyframes color-change-white {
    0% {
        background-color: #212121; }
    100% {
        background-color: #E8E8E8;
        border-color: black; } }
