* {
    margin: 0;
    padding: 0;
    box-sizing: border-box; }

$percentage : calc(100% / 6);

//#{$percentage}

.dragzone {
    width: 100%;
    height: 100%;
    position: relative;
    display: grid;
    grid-template-columns: repeat(8, 12.5%);
    grid-template-rows: repeat(6, #{$percentage});
    &:hover {
        // border: 1px solid black
        border-radius: 1rem; }
    // div:hover
    //     border: 1px solid black
    //     border-radius: 1rem

    .fa-folder {
        width: auto;
        height: auto;
        display: flex;
        flex-direction: column;
        text-shadow: 3px 3px 4px #272634;
        &:before {
            align-self: center;
            justify-self: center; } }

    p {
        text-shadow: 3px 3px 4px #b3b3b3; }

    .fa-file {
        width: auto;
        height: auto;
        display: flex;
        flex-direction: column;
        text-shadow: 3px 3px 4px #272634;
        &:before {
            align-self: center;
            justify-self: center; }

        p {}
        text-shadow: 3px 3px 4px #b3b3b3; }

    .fa-trash-alt {
        text-shadow: 8px 7px 3px #272634; }

    div {
        width: 100%;
        height: 100%;
        display: flex;
        align-items: center;
        justify-items: center;
        align-content: center;
        justify-content: center; } }
.name {
    font-size: 1rem;
    width: 68px;
    word-wrap: break-word;
    text-align: center; }

.nameInput {
    font-size: 1rem;
    width: 68px;
    height: 20px;
    border-radius: 10px;
    word-wrap: break-word;
    text-align: center; }


