.landing {
    width: 100%;
    height: 100%;
    display: flex;
    flex-flow: column;
    justify-content: center;
    align-items: center;
    background-image: url("../../media/landing.jpg");
    background-repeat: no-repeat;
    background-position: center;
    background-size: cover;

    &:after {
        width: 100px;
        height: 100px;
        background-color: red; }
    &__logo {
        position: relative;
        width: 100px;
        height: 100px;
        background-image: url('../../media/logo.png');
        background-position: center;
        background-repeat: no-repeat;
        background-size: cover; }
    &__title {
        margin: 1rem;
        font-size: 2rem;
        color: white; }
    &__advise {
        color: #ffffff;
        margin: 0.5rem;
        margin-bottom: 1rem; }
    &__login {
        width: 7rem;
        height: 2rem;
        margin: 1rem 1rem;
        border: 2px solid black;
        background-color: #313131;
        color: #e6e6e6;
        border-top-left-radius: 1rem;
        border-bottom-right-radius: 1rem;
        &:hover {
            background-color: #212121;
            color: black;
            animation: color-change-white 0.5s forwards, border-change 0.5s forwards; } }
    &__register {
        width: 7rem;
        height: 2rem;
        margin: 1rem 1rem;
        border: 2px solid black;
        background-color: #313131;
        color: #e6e6e6;
        border-top-left-radius: 1rem;
        border-bottom-right-radius: 1rem;
        &:hover {
            background-color: #212121;
            color: black;
            animation: color-change-white 0.5s forwards, border-change 0.5s forwards; } } }

@keyframes border-change {
    0% {
        border-top-left-radius: 1rem;
        border-bottom-right-radius: 1rem; }
    100% {
        border-top-left-radius: 0rem;
        border-top-right-radius: 1rem;
        border-bottom-right-radius: 0rem;
        border-bottom-left-radius: 1rem; } }

@keyframes color-change-white {
    0% {
        background-color: #212121; }
    100% {
        background-color: #E8E8E8;
        border-color: black; } }


