* {
    box-sizing: border-box;
    margin: 0; }

#email {
    &:-webkit-autofill {
        background-color: black !important; } }

#password {
        &:-webkit-autofill {
            background-color: black !important; } }

.login {
    width: 100%;
    height: 100%;
    display: flex;
    flex-flow: column;
    justify-content: center;
    align-items: flex-start;

    &__image {}
    // background-image: url("https://stmed.net/sites/default/files/black-%26-white-wallpapers-25165-6959283.jpg")
    background-image: url('../../media/lr-background.jpg');
    background-repeat: no-repeat;
    background-position: center;
    background-size: cover;
    filter: blur(0px);

    &__register {
        position: absolute;
        right: 5rem;
        cursor: pointer;
        background-color: black;
        color: white;
        max-width: 10rem;
        width: auto;
        height: auto;
        font-size: 1.2vw;
        font-weight: bold;
        padding: 1.2rem;
        border-radius: 100% 0% 90% 0% / 60% 34% 66% 50%;
        margin: 0 auto;
        text-align: center;
        position: absolute;
        animation: blurred 1s; }

    &__form {
        animation: blurred 1s;
        position: relative;
        right: 1.5em;
        display: flex;
        flex-flow: column;
        justify-content: center;
        align-items: center;
        opacity: 0.8;
        width: 320px;
        height: auto;
        margin: 8rem;
        filter: blur(0px);

        // overflow: hidden
        &:hover {
            opacity: 1; }
        div {
            position: relative;
            // overflow: hidden
            display: flex;
            flex-flow: column;
            margin: 0.5rem;
            input {
                padding: 6px 20px;
                width: 100%;
                border: 2px solid white;
                border-radius: 0.4rem;
                background: none;
                position: relative;
                top: 0;
                left: 0;
                z-index: 1;
                outline: 0;
                color: white;
                &:valid {
                    background: black !important; }
                &:focus {
                    border-color: #29293d;
                    padding: 2px 30px 10px 10px;
                    & + label {
                        top: 100%;
                        margin-top: -16px;
                        background: #29293d;
                        color: white;
                        font-size: 70%;
                        padding: 1px 6px;
                        z-index: 2;
                        text-transform: uppercase; } } }

            label {
                top: 0;
                bottom: 0;
                left: 0;
                width: 100%;
                transition: background 0.2s, color 0.2s, top 0.2s, bottom 0.2s, right 0.2s, left 0.2s;
                position: absolute;
                color: white;
                padding: 7px 6px;
                border-bottom-left-radius: 0.4rem;
                border-bottom-right-radius: 0.4rem; } } }

    &__button {
        width: 7rem;
        height: 2rem;
        margin: 1rem 1rem;
        border: 2px solid black;
        background-color: white;
        color: #212121;
        border-top-left-radius: 1rem;
        border-bottom-right-radius: 1rem;
        align-self: center;
        border-color: white;

        &:hover {
            background-color: #212121;
            color: white;
            animation: color-change-black 0.5s forwards, border-change 0.5s forwards; } } }

@keyframes border-change {
    0% {
        border-top-left-radius: 1rem;
        border-bottom-right-radius: 1rem; }
    100% {
        border-top-left-radius: 0rem;
        border-top-right-radius: 1rem;
        border-bottom-right-radius: 0rem;
        border-bottom-left-radius: 1rem; } }

@keyframes color-change-black {
    0% {
        background-color: white; }
    100% {
        background-color: #212121;
        border-color: white; } }

@keyframes blurred {
    0% {
        filter: blur(2px); }
    100% {
        filter: blur(0px); } }
